class NavbarController {
  constructor($scope, $log, $mdSidenav) {
    this.name        = 'navbar';
    this.$scope      = $scope;
    this.$mdSidenav  = $mdSidenav;
    this.$log        = $log;
  }
  openMenu(navId){
    var _this = this;
    this.$mdSidenav(navId)
      .toggle()
      .then(function () {
        _this.$log.info(`${navId} toggle`)
      });
  }
}

NavbarController.$inject = [`$scope`, `$log`, `$mdSidenav`];

export default NavbarController;



