class AppController {
  constructor($scope, $mdSidenav, $log) {
    this.name = 'hero';
    $scope.toggleLeft = (navId) => {
      $mdSidenav(navId)
        .toggle()
        .then(() => {
          $log.info(`${navId} toggle`)
        });
    }
  }
}

AppController.$inject = ['$scope', `$mdSidenav`, `$log`];

export default AppController;



