class PaperSidenavController {
  constructor($scope, $mdSidenav, $log) {
    this.name = 'paperSidenav';
    $scope.toggleLeft = (navId) => {
      $mdSidenav(navId)
        .toggle()
        .then(() => {
          $log.info(`${navId} toggle`)
        });
    }
  }
}

PaperSidenavController.$inject = ['$scope', `$mdSidenav`, `$log`];

export default PaperSidenavController;



